import { Component, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { FundoService } from '../services/utils/fundo.service';

@Component({
  selector: 'app-gercapitulos',
  templateUrl: './gercapitulos.page.html',
  styleUrls: ['./gercapitulos.page.scss'],
})
export class GercapitulosPage implements OnInit {

  img = '';

  constructor(
    private plataform: Platform,
    private fundoService: FundoService,
  ) { }

  async ngOnInit() {
    await this.plataform.ready()
      .then(() => {
        if (this.plataform.width() <= 700)
          this.fundoService.setCurrentPlataform('mobile');
        else
          this.fundoService.setCurrentPlataform('pc');

        this.img = this.fundoService.changeImg();
      });
  }

}
